<template>
  <b-card :header="$t('Products')">
    <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" @click="copyData=false,showLoader=false,clearObjects(),$refs.addProductModel.show(),setSelectedProduct()" class="mr-1 mb-2 mt-xl-n2 pull-right">{{$t('Add_Product')}}</b-button>
    <b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="gridData" :fields="captions" :current-page="currentPage" :per-page="perPage">
      <template slot="top-row" slot-scope="{ fields }"  >
        <td v-for="field in fields" :key="field.key"  >
          <input   v-model="filters[field.key]" :placeholder="' '+field.label" class="form-control" @click="currentPage=1"  v-if="field.key!='actions' && field.key!='AddFlashSell'" >
        </td>
      </template>
      <template slot="AddFlashSell" slot-scope="status">
          <!-- <label v-if="status.item.AddFlashSell=='Y'" style="color: #008000;" ><b>Yes</b></label>
          <label  v-if="status.item.AddFlashSell!='Y'" style="color: #ef402b;"><b>No</b></label> -->
          <label v-if="status.item.AddFlashSell=='0'" style="color: #008000;" ><b>{{$t('None')}}</b></label>
          <label v-if="status.item.AddFlashSell=='1'" style="color: #008000;" ><b>{{$t('Flash_Sale')}}</b></label>
          <label v-if="status.item.AddFlashSell=='2'" style="color: #008000;" ><b>{{$t('Home_Page')}}</b></label>
        </template>
      <template slot="actions" slot-scope="data">
          <a v-on:click="editMode=true,showLoader=false,clearObjects(),selectObject(data.item), $refs.editProductModel.show()" v-b-tooltip.hover :title="$t('Update')" class="icon mr-2 anchor-pointer"><i id="editIcon"  class="fa font-2x2 fa-pencil"></i></a>
           <a v-on:click="enableProduct=true,clearObjects(),selectObject(data.item),$refs.disableProductModel.show()" v-b-tooltip.hover :title="$t('Enable_Product')"  class="icon mr-2 anchor-pointer" v-if="data.item.ProductStatus=='N'"  ><i class="fa fa-check-circle font-2x2 icons mt-1"></i></a>
          <a v-on:click="disableProduct=true,clearObjects(),selectObject(data.item),$refs.disableProductModel.show()" v-b-tooltip.hover :title="$t('Disable_Product')"  class="icon mr-2 anchor-pointer" v-if="data.item.ProductStatus=='Y'"><i class="fa fa-ban font-2x2 icons mt-1"></i></a>
