    handleFileUpload: function(e) {

      var selectedFiles = e.target.files;
      this.imgsname = e.target.files[0].name;
      
      // console.log("this.imgsname",this.imgsname);

      if (/\.(jpe?g|png|gif)$/i.test(this.imgsname)) {
      for (let i = 0; i < selectedFiles.length; i++) {

        if (selectedFiles[i].size > 1024*1024) {
        e.preventDefault();
    
        alert(selectedFiles[i].name+' file should not greater than 1MB');
        this.productsImages=[];
        selectedFiles=[];
        this.saveAds.productsImages=[];
        break;
      }else{
        this.productsImages.push(selectedFiles[i]);
      }
      

      for (let i = 0; i < this.productsImages.length; i++) {
      let reader = new FileReader(); //instantiate a new file reader
      reader.addEventListener(
      "load",
      function() {
      this.$refs["image" + parseInt(i)][0].src = reader.result;
      this.showPreview = true;
      }.bind(this),
      false
      ); //add event listener

      reader.readAsDataURL(this.productsImages[i]);
      }
      }
      } 
       
},

 dataURItoBlob(dataURI) {
    var byteString = atob(dataURI.split(',')[1]);
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);
    for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: 'image/jpeg' });
},

