          // this.selectedProduct.Amount=this.selectedProduct.ProductItems[0].Amount;
          // this.selectedProduct.SellPrice=this.selectedProduct.ProductItems[0].SellPrice;
          // this.selectedProduct.PromotionalPrice=this.selectedProduct.ProductItems[0].PromotionalPrice;
    },
    getStores: function () {
      
    
     this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
        
       
        let url =null;
       if(this.loginUser.Role=="M"){
           url = 'user/store/get/'+this.loginUser.UserID;
