        this.storeList = res.data;

        //console.log("this.storesList",this.storesList);
      });
    },
   

    getProductGrp: function(store_id) {

			toutPaieService.getData("store/productGroups/"+store_id).then(res => {
        this.productGroupList = res.data;

         if(this.loginUser.Role=="M"){
        if(this.productGroupList.length>0){
                   this.selectedProduct.StoreProductGroupID=this.productGroupList[0].StoreProductGroupID;
               }
         }       
 
      });
      
    },

    defaultSelection: function(){
       
               if(this.loginUser.Role=="M"){
          if(this.storeList.length>0){
            for(var i=0;i<this.storeList.length;i++){

          this.selectedProduct.StoreID=this.storeList[i].StoreID;
           }
               this.getProductGrp(this.selectedProduct.StoreID);
               if(this.productGroupList.length>0){
                   this.selectedProduct.StoreProductGroupID=this.productGroupList[0].StoreProductGroupID;
               }
               
          }
       }
    },
     
     addDeleteProductImgs: function(img) {
       console.log(img);
      //  console.log(this.productsImages1);
       this.removeProductImgs.push(img);
