    getProducts: function() {

this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));

       let url =null;
       if(this.loginUser.Role=="M"){
           url = 'productCatalogue/group/bystore/user/'+this.loginUser.UserID;
