                     url="productCatalogue/forAdmin/get";
       }

      

			toutPaieService.getData(url).then(res => {
        this.productsList = res.data;

        // for(var i=0;i<this.productsList.length;i++){

        //   this.productsList[i].AddFlashSell=this.productsList[i].AddFlashSell=="Y"?"Yes":"No"

        // }
        


        // console.log("productsList :",this.productsList);
     
       
      });
      
    },


    checkValidation (modal) {

      // console.log("selectedProduct :",this.selectedProduct);
    
       
      if (!(this.selectedProduct && this.selectedProduct.TotalQuantity &&
        this.selectedProduct.Amount && this.selectedProduct.AvailableQuantity && this.selectedProduct.GroupName &&
        this.selectedProduct.ProductName && this.selectedProduct.PromotionalPrice && this.selectedProduct.SellPrice 
        )) {
          this.showLoader=false;
          VueNotifications.error({message: 'Please fill all mandatory fields of Profuct Info Tab.',timeout:2500});
          setTimeout(() => {
            modal.show();
          }, 0);
      }  else {
        modal.hide();
        this.showLoader=false;
        VueNotifications.success({message: 'Data Saves Successfully',timeout:2500});
         
      }
    },


      disableItemProduct: function(ref,disableProduct) {
 
      this.disableEnableProduct(ref,disableProduct);
 
    },
     


    disableEnableProduct: function(ref,selectedUser) {
      
      let url =null; 

 
      if((selectedUser.ProductStatus=="Y"||selectedUser.ProductStatus==true)){
      url="productCatalogue/disable/"+selectedUser.ProductCatalogueID;
      }else{
        url="productCatalogue/enable/"+selectedUser.ProductCatalogueID;
      }

             console.log("url",url);          

    toutPaieService.saveData(selectedUser, url).then(data => {
        // console.log('data', data);
        let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
          ref.hide();
          this.getStores();
          if(selectedUser.ProductStatus=="Y" ){
          VueNotifications.success({message:this.translateString("Product_Disabled_Successfully_Saved"),timeout:2500});
          // window.location.reload();
          }else{
             VueNotifications.success({message:this.translateString("Product_Enabled_Successfully_Saved"),timeout:2500});
            //  window.location.reload();
          }
          this.clearObjects();

        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }
      });

    }, 






    clearObjects:function(){
      this.selectedProduct={};
      this.editProduct={};
      this.otherType= [];
       this.otherType.push({
            ProductType:null,
            PromotionalPrice: null,
            SellPrice: null,
            Amount: null,
      });
      this.getProducts();
      console.log("clear....");
	if (document.getElementById('myIdUploadSave') != null && document.getElementById('myIdUploadSave') != undefined) {
      document.getElementById('myIdUploadSave').value = ''
      document.getElementById('myIdUploadEdit').value = ''
      document.getElementById('myIdUploadCopy').value = ''
	}
       this.selectedProduct={};
      this.editProduct={};
      this.productsImages=[];
      this.removeProductImgs=[],
      this.save_images=[];
      this.update_images=[];
      this.copy_images=[];
      this.category=null;

    },

    saveItem: function(ref,selectedProduct){
       console.log('in save itom function');
          
      //  console.log('selectedProduct :',selectedProduct);
       selectedProduct.ProductItems=this.otherType;
      let userData = selectedProduct;

      if(this.editMode){
            if(this.removeProductImgs.length>0){
                 for(var d=0;d<this.removeProductImgs.length;d++){

                   this.deleteProductImg(this.removeProductImgs[d]);        
                 }
            }
          }

      if((userData.PhotoURL!=null)&&(userData.PhotoURL!="")){
    
          //      for(var i=0;i<userData.PhotoURL.length;i++){
          //      userData.PhotoURL[i]=this.dataURItoBlob(userData.PhotoURL[i]);
          //  }

           if(this.editMode ||this.copyData){
                 userData.PhotoURL=[];
             }

           for(var i=0;i<this.productsImages.length;i++){
            
                  userData.PhotoURL.push(this.productsImages[i]);
               
           }
            
      }else{
         userData.PhotoURL=this.productsImages;
      }

      this.productsImages=[];
      //  console.log("userData :",userData);   
      var form = new FormData();
      

      form.append("StoreID", userData.StoreID);
      // form.append("AddFlashSell",(userData.AddFlashSell=="true"||userData.AddFlashSell==true)?"Y":"N");
       form.append("AddFlashSell",parseInt((userData.AddFlashSell==null ? 0:userData.AddFlashSell )));
      // form.append("TotalQuantity[0]",userData.TotalQuantity);
      // form.append("AvailableQuantity[0]",userData.AvailableQuantity);
      form.append("StoreProductGroupID",userData.StoreProductGroupID);
      form.append("ProductName",userData.ProductName);

      // for(var i=0;i<userData.PromotionalPrice.length;i++){
      // form.append("PromotionalPrice["+i+"]",userData.PromotionalPrice[i]);
      //  }

      // //form.append("SellPrice[0]",userData.SellPrice);
       
      //  for(var i=0;i<userData.SellPrice.length;i++){
      // form.append("SellPrice["+i+"]",userData.SellPrice[i]);
      //  }
      
      // //form.append("Amount[0]",userData.Amount);

      if(this.category!="B"){
         
         if(!userData.ProductItems){
           userData.ProductItems=this.otherType;
          }

         userData.ProductItems[0].ProductType="Any";
         userData.ProductItems[0].Amount=userData.SellPrice;

         if(parseInt(userData.SellPrice)<parseInt(userData.PromotionalPrice)){
             this.showLoader=false;
           VueNotifications.error({message: this.translateString("Promo_Price_Should_be_less_than_Sell_Price") ,timeout:2500});
           return;
         }
         userData.ProductItems[0].SellPrice=userData.SellPrice;
         userData.ProductItems[0].PromotionalPrice=userData.PromotionalPrice;

         userData.ProductItems[0].TotalQuantity=userData.TotalQuantity;
         userData.ProductItems[0].AvailableQuantity=userData.AvailableQuantity;
         userData.ProductItems[0].PriceType="Euro";
                                             
           
          form.append("ProductItems[0]",JSON.stringify(userData.ProductItems[0]));
          

      }else{


         for(var i=0;i<userData.ProductItems.length;i++){
          //  userData.ProductItems[i].ProductType="Not enterted";
           userData.ProductItems[i].PriceType="Euro";

              if(parseInt(userData.ProductItems[i].SellPrice)<parseInt(userData.ProductItems[i].PromotionalPrice)){
           VueNotifications.error({message:this.translateString("Promo_Price_Should_be_less_than_Sell_Price"),timeout:2500});
           this.showLoader=false;
           return;
         }

           userData.ProductItems[i].Amount=userData.ProductItems[i].SellPrice;
           userData.ProductItems[i].AvailableQuantity=userData.AvailableQuantity;
           userData.ProductItems[i].TotalQuantity=userData.TotalQuantity;
         }

          for(var i=0;i<userData.ProductItems.length;i++){
          form.append("ProductItems["+i+"]",JSON.stringify(userData.ProductItems[i]));
          }
       }
       
      form.append("ShortDescription",userData.ShortDescription);
      form.append("LongDescription",this.stripHtstripHtml(userData.LongDescription));
      form.append("PriceType[0]", "Rs");  
      for(var i=0;i<userData.PhotoURL.length;i++){
      form.append("PhotoURL["+i+"]",userData.PhotoURL[i]);
       }

       
      

       
      let url =  null;
      if(this.editMode){
        url="/productCatalogue/update/"+userData.ProductCatalogueID;
        this.editMode=false;
      }else{
        url='productCatalogue/save';
      }
       
       //console.log("url :",url);

      toutPaieService.saveData(form, url).then(data => {
       // console.log('SAVEdata', form);
         
       let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
          ref.hide();
          this.getProducts();
         
          this.clearObjects();
          this.editMode=false;
          this.copyData=false,
          this.showLoader=false;
          VueNotifications.success({message: this.translateString("Product_Successfully_Saved"),timeout:2500});
          //window.location.reload();
        }else{
          this.showLoader=false;
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }   
         
      });
    },




    
  },
  beforeMount(){
	   this.ApiEndPoints=toutPaieService.ApiEndPoints;
    this.getProducts();
    this.getStores(); 

  }
}
</script>
